<template>
  <header
    :style="` background-color:${
      model ? 'hsl(209, 23%, 22%)' : 'white'
    }; z-index:1000; position:absolute; width:100vw; height:100px; display:flex; align-items:center; box-shadow:${
      model
        ? 'none'
        : '0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06)'
    };`"
  >
    <v-container style="position: relative" class="py-0">
      <nav
        class="d-flex justify-space-between align-center w-100"
        :style="` color:${model ? 'white' : 'black'}`"
      >
        <li style="font-size: 22px; font-weight: 600">Where In the World?</li>
        <li style="cursor: pointer; gap: 10px" class="d-flex align-center">
          <v-switch
            v-model="model"
            hide-details
            color="primary"
            @click="toggle, updatesomeModel()"
          ></v-switch>
        </li>
      </nav>
    </v-container>
  </header>
</template>
<script>
import { useCountryStore } from "@/stores/country";
import { mapActions, mapState } from "pinia";
export default {
  data() {
    return {
      model: false,
    };
  },
  computed: {
    ...mapState(useCountryStore, ["model"]),
  },
  methods: {
    ...mapActions(useCountryStore, ["updateModel"]),
    updatesomeModel() {
      this.updateModel(this.model);
    },
  },
};
</script>

<style lang="scss">
li {
  list-style: none;
}
</style>
