<template>
  <div class="home" style="padding-top: 100px">
    <v-container>
    <div
      style="
        top: 300px;
        left: 50%;
        transform: translateX(-50%);
        position: absolute;
      "
      v-if="searchQuery!=filteredCountries && searchQuery!=''&& !filteredCountries.length">
      <img src="../assets/felshaus-info.gif" class="w-100">
    </div>
    </v-container>
    <div
      style="
        top: 50%;
        left: 50%;
        transform: translate(-50%);
        position: absolute;
      "
      v-if="!countries.length">
      <v-progress-circular
        :size="128"
        :width="6"
        color="primary"
        indeterminate
      ></v-progress-circular>
    </div>
    <v-container v-if="countries.length">
      <v-row class="justify-space-between">
        <v-col cols="12" lg="5" md="5" sm="6">
          <input
            type="text"
            placeholder="Search"
            :style="inputStyle"
            v-model="searchQuery"
            @update:model-value="filterByName(), updatefilterSearch()"
          />
        </v-col>
        <v-col cols="6" lg="4" md="5" sm="6">
          <v-select
            class="mx-0"
            label="Filter by your region"
            v-model="selectedRegion"
            :items="regions"
            @update:model-value="updateValue(), filterByRegion()"
            variant="solo"
            :bg-color="`${model ? 'white' : 'hsl(209, 23%, 22%)'}`"
          ></v-select>
        </v-col>
      </v-row>
    </v-container>
    <v-container v-if="countries.length">
      <v-row>
        <v-col
          cols="12"
          sm="6"
          md="4"
          lg="3"
          v-for="country in filteredCountries.slice(0, 8)"
          :key="country.name.common"
        >
          <v-card
            @click="
              $router.push({
                name: 'country',
                params: { name: country.cca3 },
              })
            "
            class="mt-10"
            :color="`${model ? 'white' : 'hsl(209, 23%, 22%)'}`"
          >
            <img :src="country.flags.png" alt="" height="175px" width="100%" />
            <v-card-title
              class="py-3 px-6"
              style="font-size: 16px; font-weight: 600"
              >{{ country.name.common }}</v-card-title
            >
            <v-card-text
              class="py-0 px-6"
              style="font-size: 14px; line-height: 1.8"
              >population : {{ country.population }}</v-card-text
            >
            <v-card-text
              class="py-0 px-6"
              style="font-size: 14px; line-height: 1.8"
              >region : {{ country.region }}</v-card-text
            >
            <v-card-text
              class="py-0 px-6 pb-10"
              style="font-size: 14px; line-height: 1.8"
              >capital :
              {{
                `${country.capital}`.toString("") == "undefined"
                  ? "No Capital"
                  : `${country.capital}`.toString("")
              }}</v-card-text
            >
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import { useCountryStore } from "@/stores/country";
import { mapState, mapActions } from "pinia";

export default {
  data: () => ({
    selectedRegion: "",
    searchQuery: "",
  }),
  props: {
    model: {
      type: Boolean,
    },
  },
  computed: {
    ...mapState(useCountryStore, [
      "regions",
      "filteredCountries",
      "selectedRegion",
      "countries",
      "model",
      "searchQuery",
    ]),
    inputStyle() {
      return `
        background-color: ${this.model ? "white" : "hsl(209, 23%, 22%)"};
        color: ${this.model ? "hsl(209, 23%, 22%)" : "white"};
        height: 55px;
        border-radius: 6px;
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2),
          0 6px 20px 0 rgba(0, 0, 0, 0.19);
        width: 100%;
        text-align: center;
        outline: 0;
      `;
    },
    selectBgColor() {
      return this.model ? "white" : "hsl(209, 23%, 22%)";
    },
    selectColor() {
      return this.model ? "hsl(209, 23%, 22%)" : "white";
    },
  },
  methods: {
    ...mapActions(useCountryStore, [
      "fetchCountries",
      "filterByRegion",
      "updateSomeValue",
      "filterByName",
      "updatefilterSearchs",
    ]),
    updateValue() {
      this.updateSomeValue(this.selectedRegion);
    },
    updatefilterSearch() {
      this.updatefilterSearchs(this.searchQuery);
    },
  },
  async mounted() {
    await this.fetchCountries();
  },
};
</script>

<style scoped>
.v-list.v-theme--light.v-list--density-default.v-list--one-line {
  padding: 0;
}
</style>
