import { defineStore } from "pinia";
import axios from "axios";

export const useCountryStore = defineStore("countryStore", {
  state: () => ({
    countries: [],
    filteredCountries: [],
    selectedRegion: "",
    searchQuery: "",
    model: Boolean,
    singleCountry: {}, // يجب أن يكون كائنًا
    regions: ["Antarctic", "Asia", "Europe", "Africa", "Oceania", "Americas"],
  }),
  actions: {
    updateSomeValue(val) {
      this.selectedRegion = val;
    },
    updateModel(val) {
      this.model = val;
      console.log(this.model);
    },
    updatefilterSearchs(val) {
      this.searchQuery = val;
    },
    async fetchCountries() {
      try {
        const res = await axios.get("https://restcountries.com/v3.1/all");
        this.countries = res.data;
        this.filteredCountries = res.data;
      } catch (err) {
        console.error("Error fetching countries:", err);
      }
    },
    filterByRegion() {
      if (this.selectedRegion === "") {
        this.filteredCountries = this.countries.slice(0, 8);
      } else {
        this.filteredCountries = this.countries.filter(
          (country) => country.region === this.selectedRegion.slice(0, 8)
        );
      }
    },
    async getSingleCountry(name) {
      try {
        const res = await axios.get(
          `https://restcountries.com/v3.1/alpha/${name}`
        );
        this.singleCountry = res.data[0];
      } catch (err) {
        console.error("Error fetching countries:", err);
      }
    },
   async filterByName() {
       this.filteredCountries =await this.countries.filter((country) =>
          country.name.common
            .toLowerCase()
            .includes(this.searchQuery.toLowerCase())
        );
    },
  },
});
